.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

/* --------------------------------------------------------
   UTSA GLOBAL header, footer, responsive breakpoint and top level content styles
   -------------------------------------------------------- */
html,
body {
  height: 100%;
  font-family: "ff-meta-web-pro",sans-serif; }

body {
  -webkit-font-smoothing: anialiased;
  text-rendering: optimizeLegibility; }

a {
  /*15px;*/
  transition: all .5s ease-in;
  -webkit-transition: all .3s ease-in;
  -moz-transition: all .3s ease-in;
  -ms-transition: all .3s ease-in;
  -o-transition: all .3s ease-in; }

/*i {
    transition: all .5s ease-in; -webkit-transition: all .3s ease-in; -moz-transition: all .3s ease-in; -ms-transition: all .3s ease-in; -o-transition: all .3s ease-in;
}*/
a:hover {
  text-decoration: none; }

.orangetext {
  color: #f15a22; }

a:hover,
h1 a,
h2 a,
h3 a {
  color: #f15a22; }

h1,
h2,
h3,
h4,
h5 {
  font-weight: 700;
  margin: .3em 0 .6em; }

.bg-inverse {
  color: #fff;
  background-color: #0c2340; }

#banner {
  background-color: #0c2340;
  border-bottom: 3px solid #f15a22; }

.logo-only div nav a {
  padding-top: 1.1rem; }

.linkmobile {
  font-size: .68rem;
  color: #fff;
  transition: all 0.3s ease 0s; }

.linkmobile:hover {
  color: #f15a22; }

.top-link {
  font-size: 1.1rem; }

.navbar-nav .nav-link {
  font-size: .95rem; }

.toggle {
  font-size: 32px; }

div.aboutBanner {
  height: 600px; }

div.parallaxBanner {
  height: 400px; }

.jumbotron-heading {
  text-align: left;
  color: #fff;
  margin-top: 1.9em;
  margin-bottom: 0;
  font-size: 7em;
  text-shadow: 2px 2px 8px #101010;
  font-weight: 600;
  letter-spacing: -4px; }

.jumbotron {
  padding-top: 6rem;
  padding-bottom: 6rem;
  background-color: #fff;
  background-repeat: no-repeat;
  background-position: 50% 25%;
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
  height: 450px;
  background-color: white;
  background-size: 100%;
  border-radius: 0; }

.toptext h2 {
  color: #0c2340;
  line-height: 2.3rem;
  padding-top: 1.5rem;
  padding-bottom: 2rem;
  font-weight: normal !important; }

/*Search DropDown*/
.headerSearchContainer {
  display: none;
  background: #0c2340; }

.headerSearchContainer input {
  left: 5%;
  padding: 0.8em 0;
  margin: 0.8em 0 0.8em 0;
  text-indent: 15px;
  width: 90%;
  border: 1px solid #999;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  background: #fff; }
  .headerSearchContainer input .wrapper:after {
    float: right;
    font-family: 'FontAwesome';
    color: #f15a22;
    font-size: 1.25em;
    top: 22px;
    right: 7%;
    position: relative;
    content: "\f002"; }
  .headerSearchContainer input .wrapper input[type="text"] {
    position: relative; }

#headerSearchCTA {
  color: #f15a22; }

.showSearch {
  display: block; }

.hideSearch {
  display: none; }

#logoFull {
  width: 320px;
  max-width: 320px; }

.logo {
  width: 110px !important;
  max-width: 110px !important; }

.wrapper label:before {
  border-left: 0px solid transparent; }

.wrapper label i.fa {
  color: #f15a22; }

#backToTop {
  background: #f15a22; }
  #backToTop a {
    color: #fff;
    font-size: 18.25pt; }

#global-footer {
  background: #0c2340;
  color: #fff;
  border-top: 3px solid #f15a22;
  font-size: 0.75rem; }
  #global-footer .global-footer-nav a, #global-footer .global-footer-info a, #global-footer .global-footer-copy a, #global-footer .global-footer-social a {
    color: #fff; }
  #global-footer .global-footer-nav a:hover, #global-footer .global-footer-info a:hover, #global-footer .global-footer-copy a:hover, #global-footer .global-footer-social a:hover {
    color: #f15a22; }
  #global-footer .gray, #global-footer .gray a {
    color: #8B8B8B;
    background-color: #0c2340; }
  #global-footer .utsa-social-circle {
    font-size: .9rem;
    width: 1.6rem;
    height: 1.6rem;
    line-height: 1.6;
    float: none;
    border-radius: 50%;
    border: 2px solid #fff; }

.border-dark {
  border-color: #223247 !important; }

.dropdown-divider {
  margin: .25rem 0; }

@media screen and (max-width: 767px) {
  #startupNavbar ul li a {
    font-size: 1.25rem; }
  .dropdown-divider {
    margin: .5rem 0; } }

@media screen and (min-width: 340px) and (max-width: 767px) {
  .logo {
    width: 80px !important; } }

@media screen and (max-width: 340px) {
  .logo {
    width: 63px !important; } }

@media screen and (min-width: 991px) {
  .moz-fix {
    min-height: 25px !important; } }
